import React from "react";
import PropTypes from "prop-types";
import {withStyles} from "@material-ui/core/styles";
import Toolbar from "@material-ui/core/Toolbar";
import AppBarBase from "@material-ui/core/AppBar";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import withWidth from "@material-ui/core/withWidth";
import Hidden from '@material-ui/core/Hidden';
import {IntlProvider, injectIntl} from "react-intl";
import {compose} from 'redux';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';

const styles = theme => ({
  root: {
    flexGrow: 1
  },
  menuButton: {
    marginRight: theme.spacing(2)
  },
  title: {
    flexGrow: 1
  },
  toolbar: {
    width: "100%",
    maxWidth: theme.layout.contentMaxWidth,
    margin: "0 auto"
  }
});

const AppBar = ({classes, children, history, width}) => {

  const [anchorEl, setAnchorEl] = React.useState(null);

  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  return (<div className={classes.root}>
    <AppBarBase className={classes.appBar} position="static">
      <Toolbar className={classes.toolbar}>
        <Hidden only="xs">
          <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
            <MenuIcon/>
          </IconButton>
        </Hidden>
        <Typography variant="h6" className={classes.title}>
          {width}
          EDUAGG
        </Typography>
        <Button color="inherit">поиск</Button>
        <Button color="inherit" onClick={handleClick}>
          Аккаунт
        </Button>
        <Menu id="simple-menu" anchorEl={anchorEl} keepMounted="keepMounted" open={Boolean(anchorEl)} onClose={handleClose}>
          <MenuItem onClick={handleClose}>Вход</MenuItem>
          <MenuItem onClick={handleClose}>Регистрация</MenuItem>
        </Menu>
      </Toolbar>
    </AppBarBase>
  </div>);
};

AppBar.propTypes = {
  classes: PropTypes.objectOf(PropTypes.string),
  children: PropTypes.node,
  history: PropTypes.object,
  width: PropTypes.oneOf(['lg', 'md', 'sm', 'xl', 'xs']).isRequired
};

export default compose(withWidth({initialWidth: 'lg'}), withStyles(styles))(AppBar);
